/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/

.widget {
	margin: 0 0 $base__space-unit;

	/* Make sure select elements fit in widgets. */
	select {
		max-width: 100%;
	}
}

/* Search widget. */
.widget_search .search-submit {
	display: none;
}

.widget_archive{

	ul{
		list-style: none;
		padding: 0;

		li a{
			text-decoration: none;
		}

	}
}

.widget-area {

	i.icon{
		color:black;
	}
}
